body{
  padding-top:40px;
}

/* movie list */


.top-margin-navbar{
  margin-top:400px;
}
.movie-card-container .movie-card-top{
  display:flex;
}
.description {
  width:100px;
  overflow: hidden;
  height: 100px;

}
.spacer {
  height: 50px;
}

.bold-text{
  font-weight: bold;
}


/* navbar */
.navbar-background{
  background-color:black;
  height:49px;
  width:100%;
  position:absolute;
}
a, button, span {
  color:rgb(0, 0, 0);
  text-decoration: none;
}
body {
  margin: 0;
  padding: 0;
}

.header {
  padding: 20px
}

nav {
  width: 100%;
  margin: 0;
  top: 0;
  background: #000000;
  color: #ccc;
  z-index:10;
}

ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

li {
  font-size: 1.6em;
  box-sizing: content-box;
  flex: 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: all 0.2s ease-in;
  cursor: pointer;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

li:hover {
  background: #848484;
  color: #000000;
}


.fixed-nav {
  z-index: 10;
  position: fixed;
}

#fixed-stepper {
  width:100%;
  z-index: 10;
  position: fixed;
}

.content {
  width: 100vw;
  margin: 0 auto;
  text-align: justify;
  padding-top: 20px;
}

.content > div {
  margin: 10px 0;
}

/* Style the navbar */
#navbar {
  overflow: hidden;
  background-color: rgb(0, 0, 0);
}

/* Navbar links */
#navbar a {
  float: left;
  display: block;
  color: #ffffff;
  text-align: center;
  padding: 14px;
  text-decoration: none;
  text-decoration: 'none';
  color: #ffffff; 
  width:100 
}

/* Page content */
.content {
  padding: 16px;
}

/* The sticky class is added to the navbar with JS when it reaches its scroll position */
.sticky {
  z-index: 10;
  position: fixed;
  top: 0;
  width: 100vw;
}

/* Add some top padding to the page content to prevent sudden quick movement (as the navigation bar gets a new position at the top of the page (position:fixed and top:0) */
.sticky + .content {
  padding-top: 60px;
}


/* Modal */
.modal-row{
  display: flex;
  flex-direction: column;

}

.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	z-index: -1;
}
.upload-button-modal{
  display:flex;
  justify-content: center;
}
.inputfile + label {
  margin-left:9px;
  margin-right:9px;
  margin-top: 15px;
  font-size: 1.25em;
  font-weight: 700;
  color: rgb(114, 114, 114);
  background-color:white;
  display: block;
  width:180px;
  height: 57px;
  text-align: center;
  padding-top:15px;
  border:1px solid rgb(194, 194, 194);
  border-radius: 5px;
}


.inputfile:focus + label,
.inputfile + label:hover {
  background-color: rgb(215, 215, 215);
}
.inputfile + label {
	cursor: pointer; /* "hand" cursor */
}
.inputfile:focus + label {
	outline: 1px dotted #000;
	outline: -webkit-focus-ring-color auto 5px;
}


#alert-dialog-slide-title {
  text-align: center;
  margin-top:10px;
}
#add-movie-form{
display:flex;
justify-content: center
}
.form-buttons {
  margin-left: 70px
}

/* detail */

.detail-top-container{
  display:flex;
  flex-direction: row;
}
.detail-text-container{
  display: flex;
  flex-direction: column;
  width:100%;
  padding-left:30px;
 
}
.detail-text-container h1{
  margin-top: 20px;
  margin-bottom: 0px;
  font-size: 1.7em;
  max-width: 100px;
}
.star-container{
  margin-top: -20px
}
.star {
  font-size: 0.89em;
  margin-top:-13px;
}
.detail-image-container{
  height: 400px;
  width:271;
  border: 1px solid rgb(215, 215, 215);
  padding: 3px;
}
.image-detail {
  height: 400px;
  object-fit: cover;
  max-width:300px;
}
iframe {
  width: 100%;
  height: 100%;

}
.trailer-container{
  height: 300px;
}
.trailer-container-father{
  display:flex;
  justify-content: center;
}
.poster-container-father{

}
.poster-detail{
  max-width: 100%;
  object-fit: cover;
}
.p-container {
  height: 100%;
  width:81%;
  font-size: 0.9em;
  margin-top: -50;
}
.p-container h4{

  margin-top: 6px;
  margin-bottom: 0;
}

.detail-button-container{
  display:flex;
  flex-direction: column;
}

.detail-button{
  margin:5px;
  display: block;
  border:1px solid rgb(194, 194, 194);
  text-align: center;
  width: 100px;
  height: 25px;
  padding: 16px 10px 10px 10px;
  text-align: center;
  border-radius: 5px;
  color:rgb(114, 114, 114);
  font-weight: bold;
  text-decoration: none;
  margin-top: 10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.detail-button:hover{
  background-color: rgb(215, 215, 215);

}


.detail-link{
  padding: 11px 0px 11px 20px ;
  display: block;
  margin-top: 20px;
  color:black;
  text-decoration: underline;
  font-size: 0.8em;
  text-align: center;
}



/* Edit form */
.edit-buttons-container{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.spinner{
  width:50px;
  height:50px;
  margin-right:25px;
  margin-top: 10px;
}
.edit-container{
  display:flex;
  flex-direction: column;
}

.edit-container  h2{
  padding-left:10px;

}
.edit-form-label-container{
  display:flex;
  justify-content: space-evenly;
  flex-direction: column;
}

.edit-form-label-container .label-for-edit :hover{
  background-color: rgb(215, 215, 215);
}

.edit-form-label-container .label-for-edit{
  display: block;
  border:1px solid rgb(194, 194, 194);
  text-align: center;
  width: 200px;
  height: 25px;
  padding: 11px 10px 10px 10px;
  text-align: center;
  border-radius: 5px;
  color:rgb(114, 114, 114);
  font-weight: bold;
  text-decoration: none;
  margin-top: 30px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
#button-edit{
  color:white;

  margin-top:7px;
}

#button-submit{
  color: white;
  margin-left:30px;
  font-size: 3em;
}
.MuiButton-label{
  margin-top:3px;
  font-size:1em;
}


/* Media Queries */
/* For edit */
@media screen and (max-width: 604px) {
  .MuiButton-label{
    font-size:0.4em;
  }
  
}
@media screen and (max-width: 516px) {
  .edit-form-label-container .label-for-edit{
    padding-top:14px;
    font-size:1em;
  }
  
}
@media screen and (max-width: 456px) {
  .edit-form-label-container .label-for-edit{
    padding-top:21px;
    font-size:0.7em;
  }
  
}
@media screen and (max-width: 410px) {
  .edit-form-label-container .label-for-edit{
    padding-top:10px;
    font-size:0.7em;
  }
  
}


/* For Detail */
@media screen and (min-width: 730px) {
  .trailer-container{
    height: 300px;
    width:600px;
  }
  
}
@media screen and (max-width: 684px) {
  #disappear{
    display:none;
  }

  .detail-top-container{
    display:flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .detail-text-container{
    padding: 0;
    align-items: center;
  }
  .detail-link{
    padding: 11px 11px 11px 11px ;
  }
}
@media screen and (max-width: 488px) {
  .p-container {
    width: 250px;

  }
  
  
}
@media screen and (max-width: 422px) {
  .detail-top-container h1{
    font-size: 1.4em
  }
  
}
@media screen and (max-width: 367px) {
  .image-detail {
    height: 350px;
    object-fit: cover;
  }
  
}

